.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border: none;
  border-radius: 0.3rem;
  font-family: "Nunito", sans-serif;
  font-weight: 500;
  text-align: center;
  cursor: pointer;
  transition: background-color 0.4s ease-in-out, box-shadow 0.4s ease-in-out;
}
.button.button--md {
  font-size: 1rem;
  padding: 0.5rem 1rem;
}
.button.button--sm {
  font-size: 0.875rem;
  padding: 0.25rem 0.5rem;
}
.button.button--lg {
  font-size: 1.125rem;
  padding: 0.65rem 1.5rem;
}
.button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.button--primary {
  background-color: #FF9F1A;
  color: #ffffff;
}
.button--primary:hover:not(:disabled) {
  background-color: rgb(230, 133.5807860262, 0);
}

.button--secondary {
  background-color: #FFB516;
  color: #ffffff;
}
