rder-color: #d8dfe7;\n$body-bg-color: #f8f7fa;\n\n// Alerts\n$alert-danger-light: #FF748B;\n$alert-danger-dark: #F72C5B;\n$alert-success: #54C392;\n$alert-warning: #ffc107;\n$alert-info: #17a2b8;\n\n// Gradients\n$gradient-primary: linear-gradient(90deg, $primary-color, $secondary-color);\n$gradient-secondary: linear-gradient(90deg, $accent-color, $highlight-color);\n\n/*=============================================================\n=                   Typography Variables                      =\n=============================================================*/\n\n$font-family-base: \"Nunito\", sans-serif;\n$font-optical-sizing: auto;\n$font-weight-base: 300;\n$font-style-base: normal;\n$letter-spacing-base: 1.2px;\n$line-height-base: 1.6;\n$font-size-base: 1rem;\n$font-color-base: #6e708c;\n\n// Headings\n$heading-font-weight: 600;\n$h1-size: 2.5rem;\n$h2-size: 2rem;\n$h3-size: 1.75rem;\n$h4-size: 1.5rem;\n$h5-size: 1.25rem;\n$h6-size: 1rem;\n\n// Text Colors\n$text-muted: #6c757d;\n$text-primary: $primary-color;\n$text-secondary: $subtitle-color;\n$text-white: $white;\n$text-black: $black;\n\n/*=============================================================\n=                   Spacing Variables                         =\n=============================================================*/\n\n$spacer: 1rem;\n$spacer-xs: 0.25rem;\n$spacer-sm: 0.5rem;\n$spacer-md: 1.5rem;\n$spacer-lg: 2rem;\n$spacer-xl: 3rem;\n\n/*=============================================================\n=                   Breakpoints Variables                     =\n=============================================================*/\n\n$breakpoint-xs: 320px;\n$breakpoint-sm: 576px;\n$breakpoint-md: 768px;\n$breakpoint-lg: 992px;\n$breakpoint-xl: 1200px;\n$breakpoint-xxl: 1400px;\n\n/*=============================================================\n=                   Boxes & Cards Variables                   =\n=============================================================*/\n\n$shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;\n$shadow-lg: rgba(0, 0, 0, 0.35) 0px 10px 20px;\n$shadow-sm: rgba(0, 0, 0, 0.15) 0px 1px 3px;\n$border-radius: 0.3rem;\n$border-radius-sm: 0.2rem;\n$border-radius-lg: 0.5rem;\n\n/*=============================================================\n=                   Z-Index Variables                         =\n=============================================================*/\n\n$z-index-dropdown: 1000;\n$z-index-sticky: 1020;\n$z-index-fixed: 1030;\n$z-index-modal: 1040;\n$z-index-tooltip: 1050;\n\n/*=============================================================\n=                   Transition Variables                      =\n=============================================================*/\n\n$transition-fast: 0.2s ease-in-out;\n$transition-normal: 0.4s ease-in-out;\n$transition-slow: 0.6s ease-in-out;\n\n/*=============================================================\n=                   Utility Variables                         =\n=============================================================*/\n\n$max-width-container: 1140px;\n","@import '../../styles/base/_variables';\n\n// Base styles for the button\n.button {\n  display: inline-flex;\n  align-items: center;\n  justify-content: center;\n  border: none;\n  border-radius: $border-radius;\n  font-family: $font-family-base;\n  font-weight: 500;\n  text-align: center;\n  cursor: pointer;\n  transition: background-color $transition-normal, box-shadow $transition-normal;\n  \n  // Default size (md)\n  &.button--md {\n    font-size: $font-size-base; // from _variables.scss (1rem)\n    padding: 0.5rem 1rem; // Adjust as needed\n  }\n\n  // Small button size\n  &.button--sm {\n    font-size: 0.875rem;\n    padding: 0.25rem 0.5rem;\n  }\n\n  // Large button size\n  &.button--lg {\n    font-size: 1.125rem;\n    padding: 0.65rem 1.5rem;\n  }\n  \n  &:disabled {\n    opacity: 0.6;\n    cursor: not-allowed;\n  }\n}\n\n// Variant: Primary\n.button--primary {\n  background-color: $primary-color;\n  color: $white;\n\n  &:hover:not(:disabled) {\n    background-color: darken($primary-color, 10%);\n  }\n}\n\n// Variant: Secondary\n.button--secondary {\n  background-color: $secondary-color;\n  color: $white;\n\n  &:hover:not(:disabled) {\n    background-color: darken($secondary-color, 10%);\n  }\n}\n\n// Variant: Accent\n.button--accent {\n  background-color: $accent-color;\n  color: $white;\n\n  &:hover:not(:disabled) {\n    background-color: darken($accent-color, 10%);\n  }\n}\n\n// Variant: Danger\n.button--danger {\n  background-color: $alert-danger-dark;\n  color: $white;\n\n  &:hover:not(:disabled) {\n    background-color: darken($alert-danger-dark, 10%);\n  }\n}\n\n// Variant: Success\n.button--success {\n  background-color: $alert-success;\n  color: $white;\n\n  &:hover:not(:disabled) {\n    background-color: darken($alert-success, 10%);\n  }\n}\n"],"sourceRoot":""}]);
